<template>
    <div>
        <div class="my-popups">
            <div class="modal d-block ">

                <div class="modal-dialog ui-draggable" style="display: inline-block;">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-26" class="modal-title">{{ $t('Comments') }} {{ commentData.commentTitle }}</h5><button
                                type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i
                                    class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div id="backgroundPopup" class="modal-body"
                            style="height: auto; min-height: 0px; max-height: none;">
                            <section id="main-content" class="main-content">
                                <section class="main-content">
                                    <div class="container-fluid p-0">
                                        <div class="p-0">

                                            <ValidationObserver ref="form" v-slot="{ handleSubmit }">
                                                <form v-on:submit.prevent="handleSubmit(submit)" ref="form">

                                                    <div class="row">
                                                        <div class="col-md-12">
                                                            <ValidationProvider rules="required" v-slot="{ errors }"
                                                                class="tt_textarea_span position-relative" name="title">
                                                                <label>Title:<span class="mandatory">*</span></label>
                                                                <textarea :placeholder="$t('WriteAComment')"
                                                                    v-model="commentData.commentTitle" name="title"
                                                                    class="form-control "
                                                                    :class="errors[0] ? 'is-invalid' : ''"
                                                                    />
                                                                <span class="invalid-feedback">{{ errors[0] ?
                                                                    $t('CommentIsReq') : '' }}</span>
                                                            </ValidationProvider>

                                                        </div>
                                                    </div>
                                                    <div class="row">
                                                        <div class="col-md-12">
                                                            <ValidationProvider rules="required" v-slot="{ errors }"
                                                                class="tt_textarea_span position-relative" name="Comment">
                                                                <label>Comments:<span class="mandatory">*</span></label>
                                                                <textarea :placeholder="$t('WriteAComment')"
                                                                    v-model="commentData.comment" name="Comment"
                                                                    class="form-control "
                                                                    :class="errors[0] ? 'is-invalid' : ''"
                                                                    style="border-color: #BDBCBD; resize:none"
                                                                    maxlength="2000"></textarea>
                                                                <span class="invalid-feedback">{{ errors[0] ?
                                                                    $t('CommentIsReq') : '' }}</span>
                                                            </ValidationProvider>

                                                        </div>
                                                    </div>

                                                    <div class="row flex-row-reverse mt-2">

                                                        <div class="text-end text-right col-12">
                                                            <button type="submit" class="btn btn-success"><i
                                                                    class="fa fa-save pr-2"></i>{{ $t('Save') }}
                                                            </button>
                                                            <button @click="$emit('close')" type="button"
                                                                class="btn btn-danger" style="margin-left: 5px;"><i
                                                                    class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
                                                            </button>
                                                        </div>

                                                    </div>

                                                </form>
                                            </ValidationObserver>
                                        </div>
                                    </div>
                                </section>
                                <div class=" col-12 tab-pane fade active show">
                                    <div v-if="commentList.length.length == 0"
                                        class="row ticket-card-container card-view bg-border p-3">
                                        <div class="col-sm-12 col-md-12 col-lg-12 mt-2 text-center">
                                            <span class="text-danger ">NoComments</span>
                                        </div>
                                    </div>
                                </div>
                                <div class="listing mt-4" id="divconversationbox" v-if="commentList.length > 0">

                                    <div class="listing tab-content">
                                        <div class="col-12 d-flex border border-bottom-0 bg-f5f5f5">
                                            <div class="col-md-6 pl-0">
                                                <h6 class="py-3 mb-0">
                                                    Comments
                                                </h6>
                                            </div>
                                            <div class="col-md-6 text-right pr-0 bottom_filter_button">
                                                <a href="javascript:;" id="ancreplyExpandAllCollapseAll"
                                                    class="position-relative" style="z-index:999;" title=""
                                                    data-toggle-tooltip="tooltip"
                                                    data-original-title="Expand All or Collapse All"
                                                    @click="expendOrcollapse">Expand All or Collapse All</a>
                                            </div>
                                        </div>
                                        <div class="tab-pane fade show activity-tab active pt-2"
                                            style="overflow-y:auto;max-height:500px;" v-for="(items, index) in commentList"
                                            :key="index">
                                            <div id="accordion" class="w-100">
                                                <div class="card">
                                                    <div class="card-header">
                                                        <a class="card-link" data-toggle="collapse"
                                                            href="#collapseOne1requestcomment1"
                                                            @click="toggleCollapse(index)" aria-expanded="true">
                                                            <div class="conversation-date heading-border"><span><i
                                                                        class="fa fa-angle-down rotate-icon pr-1"
                                                                        aria-hidden="true"></i>{{
                                                                            dateFormat(items.dateGroup) }}</span></div>
                                                        </a>
                                                    </div>
                                                    <div id="collapseOne11" v-if="isCollapsed(index)" class="collapse show" data-parent="#accordion"
                                                        style="">
                                                        <div class="card-body">
                                                            <div class="conversation-main" v-for="(data,i) in items.trackEntryCommentList" :key="i">
                                                                
                                                                <div class="conversation-time"><span>{{timeFormat(data.createD_AT)}}</span></div>
                                                                <div class="conversation-box-con" >
                                                                    <div class="conversation-box">
                                                                        <div class="name-circle light-yellow">
                                                                            <img class="img-circle avatar"
                                                                                :src="data.avatar"
                                                                                width="51" height="51" alt="Image">
                                                                        </div>
                                                                        <div class="conversation-header addednote-ticket">
                                                                            <span>
                                                                                <span class="sender-name">{{data.tracK_ENTRY_COMMENT_TITLE}}</span>
                                                                                <span class="sender-department">
                                                                                    ({{data.tracK_ENTRY_COMMENT_DESC}})</span>
                                                                            </span>
                                                                        </div>
                                                                        <div class="conversation-content">
                                                                            <div class="col-12 p-0 notesdata">
                                                                                <h6 class="text-muted time"><i><b>{{ data.createD_BY }}</b>, Posted on: <span
                                                                                            class="text-danger"
                                                                                            title="12/21/2023 10:09:17 AM UTC">{{ data.createD_AT }}</span> </i></h6>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </section>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>
    
<script>
import { defineComponent, } from '@vue/composition-api'
import { ValidationProvider, extend, ValidationObserver } from 'vee-validate';
import DataService from '@/services/DataService';
import VueTimepicker from 'vue2-timepicker'
import { getSecondsFromDate, getPreviousDay } from "@/assets/common/vue-common.js"
import moment from "moment"
extend('required', {

    validate(value) {
        console.log("checkk")
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };

    },
    //message: 'The {_field_} is required'

    ///computesRequired: true
});
export default defineComponent({
    name: "Flag comment",
    components: {
        ValidationProvider,
        ValidationObserver,
        VueTimepicker,

    },
    props: {
        flagData: {
            type: Object
        },
        store: {
            type: Object
        },
        trackIds:{
            type:String
        }
    },
    data() {
        return {
            collapsedSections: [false],
            expendCollapsedSections: [false],
            commentList: [],
            commentData:{
                commentTitle:'',
                comment:'',
                type: "singleComment"
               
            }
        }

    },
    created: function () {
        this.fetchData(this.trackIds);
        this.toggleCollapse(0)
    },
    methods: {
        async submit(){
            this.commentData.trackIds = this.trackIds
            try{
           const { data } = await DataService.FlagsPostComment(this.commentData)
           console.log("data",data)
           if(data!=null){
            this.ShowAlert(this.$t('CommentHasBeenPostedSuccessfully'), "Success", true, this.$t('Alert'));
           this.$emit('refreshList')
           }
           else{
            vm.ShowAlert($t("Something_Wrong"), 'failure', true, $t('Alert'));
           }
           this.$emit('close')
            }catch(e){

            }

        },
        isCollapsed(index) {
            // Check if the section at the given index is collapsed
            return this.collapsedSections[index];
        },
        toggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.collapsedSections, index, !this.collapsedSections[index]);
        },

        fetchData(id) {
            var params = `trackId=${id}`
            DataService.GetCommentListBytrackId(params).then(response => {
                debugger;
                this.commentList = response.data
                console.log("comments", this.commentList);
            })
        },

        dateFormat(value) {
            return getPreviousDay(value)
        },
        timeFormat(value) {
            return moment(value).format('HH:mm')
        },
        expendOrcollapse() {
            let collapsOn = true
            this.collapsedSections.map((item) => {
                if (item) {
                    collapsOn = false
                }
            })
            if (collapsOn) {
                this.collapsedSections = []
                this.commentList.map((items, index) => {
                    this.toggleCollapse(index)
                })
            } else {
                this.collapsedSections = []
            }

        },
       

    },

});
</script>
    