function  bindleftCommonSearchdropdown(instance, filtername) {
    var vm = instance;
    if(filtername == 'CA'){
        DataService.GetUsersByCompanyId().then(response=>{
            response.data.forEach(item=>{
            let obj1={
                name:item.username,
                value:item.user_id
                }
                vm.leftSearchFields[0].listOptions.push(obj1);
            })
        }).catch(err =>{
            console.log(err);
        })
    }
    else if(filtername == 'PM'){
        DataService.ListTeamUsersByPM().then(response=>{
            response.data.forEach(item=>{
            let obj1={
                name:item.username,
                value:item.user_id
                }
                vm.leftSearchFields[0].listOptions.push(obj1);
            })
        })
    }
    DataService.GetProjectListByUserIdAndUserType().then(response=>{
        response.data.forEach(item=>{
        let obj1={
            name:item.projectname,
            value:item.projectid
            }
            vm.leftSearchFields[1].listOptions.push(obj1);
        })
    })
}


export default { Headers,buttons,leftsearchSchema,bindleftCommonSearchdropdown};
